interface ParamsObject {
    [key: string]: string;
}

export default class Router {
    private parameters: ParamsObject;
    private baseURL: string;

    constructor() {
        this.baseURL = this._calcBaseURL();
        this.parameters = this._getParameters();
    }

    private _calcBaseURL(): string {
        return location.protocol + '//' + location.host + location.pathname;
    }

    private _getParameters(): ParamsObject {
        const fullSearchString: string = location.search;
        const fullParametersString: string = fullSearchString.substr(1);
        const splitParameters: string[] = fullParametersString.split('&');
        const parameters: ParamsObject = {};
        splitParameters.forEach((parameterString) => {
            const splitParamValue: string[] = parameterString.split('=');
            parameters[splitParamValue[0]] = splitParamValue[1];
        });
        return parameters;
    }

    public getParameter(key: string): string {
        this.parameters = this._getParameters();
        return this.parameters[key];
    }

    public setParameter(key: string, value: string): void {
        this.parameters[key] = String(value);
    }

    public clearParameters(): void {
        this.parameters = {};
    }

    public applyParameters(documentTitle?: string): void {
        if (!documentTitle) {
            documentTitle = document.title;
        }

        window.history.pushState({}, documentTitle, this._createNewURL(this.baseURL, this.parameters));
        document.title = documentTitle;
        window.dispatchEvent(new Event('popstate'));
    }

    private _createNewURL(baseURL: string, parameters: ParamsObject) {
        let splitParameters: string[] = [];

        Object.entries(parameters).forEach(([key, value]) => {
            if (value) {
                splitParameters = [...splitParameters, `${key}=${value}`];
            }
        });

        const fullParametersString = splitParameters.join('&');

        if (fullParametersString) {
            return `${baseURL}?${fullParametersString}`;
        } else {
            return baseURL;
        }
    }
}
