export const _calculateDateStringFromDate = (date: Date): string => {
    if (date) {
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        return `${year}-${month < 10 ? `0${month}` : month}-${day < 10 ? `0${day}` : day}`;
    } else {
        return '';
    }
};

export const _convertTimeStringToDate = (time: string, baseDate: Date): Date => {
    const newDate = new Date(baseDate);
    newDate.setHours(parseInt(time.split(':')[0]));
    newDate.setMinutes(parseInt(time.split(':')[1]));
    newDate.setSeconds(0, 0);
    return newDate;
};

export const _convertDateToTimeString = (date: Date): string => {
    if (date) {
        const hours = date.getHours();
        const minutes = date.getMinutes();
        return `${hours < 10 ? `0${hours}` : hours}:${minutes < 10 ? `0${minutes}` : minutes}`;
    } else {
        return '';
    }
};

export const _convertDecimalToClock = (decimalTime: number): string => {
    if (decimalTime === 24) {
        return '24:00';
    }
    const n = new Date(0, 0);
    n.setMinutes(+decimalTime * 60);
    const result = n.toTimeString().slice(0, 5);
    return result;
};

export const _substractTimeStringFromDate = (date: Date, time: string): void => {
    date.setHours(date.getHours() - parseInt(time.split(':')[0]));
    date.setMinutes(date.getMinutes() - parseInt(time.split(':')[1]));
};

export const _calculateNettoDecimal = (startTime: Date, endTime: Date, breaks: string[]): number => {
    const endTimeClone = new Date(endTime);
    breaks.forEach((_break) => {
        _substractTimeStringFromDate(endTimeClone, _break);
    });
    let hours: number;
    if (endTimeClone < startTime) {
        hours = Math.abs(startTime.getTime() - endTimeClone.getTime()) / 36e5;
        hours = -hours;
    } else {
        hours = Math.abs(endTimeClone.getTime() - startTime.getTime()) / 36e5;
    }
    return hours;
};

export const getLocale = (supportedLanguages: string[]): string => {
    let language = 'en-US';
    if (navigator.languages != undefined) {
        language = navigator.languages[0];
    } else {
        language = navigator.language;
    }
    if (!supportedLanguages.includes(language)) {
        return 'en-US';
    } else {
        return language;
    }
};

export const _calculateFormattedDateString = (locale: string, date: Date): string => {
    if (date) {
        const options1: Intl.DateTimeFormatOptions = {
            weekday: 'short',
            year: 'numeric',
            month: 'long',
            day: 'numeric',
        };
        const dateTimeFormat1 = new Intl.DateTimeFormat(locale, options1);
        const formattedDate = dateTimeFormat1.format(date);
        return formattedDate;
    }
};

export const _calculateMonthName = (locale: string, date: Date): string => {
    if (date) {
        const options1: Intl.DateTimeFormatOptions = { month: 'long' };
        const dateTimeFormat1 = new Intl.DateTimeFormat(locale, options1);
        const formattedDate = dateTimeFormat1.format(date);
        return formattedDate;
    }
};
