import { LitElement } from 'lit-element';
import { PendingContainer } from './pendingContainer';
import { RequesterEvent } from './requester';

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
export const Provider = (Base: typeof LitElement) =>
    class Provider extends PendingContainer(Base) {
        __instances = new Map();

        constructor() {
            super();

            this.addEventListener('request-instance', (event: RequesterEvent) => {
                const detail = event.detail;
                const key: string = detail.key;
                if (this.__instances.has(key)) {
                    detail.instance = this.__instances.get(key);
                    event.stopPropagation();
                }
            });
        }

        provideInstance(key: string, instance: unknown) {
            this.__instances.set(key, instance);
        }
    };
