import { nothing } from 'lit-html';
import { LitElement, html, customElement, property, css, TemplateResult, CSSResult } from 'lit-element';
import pickerStyles from '../components/picker-styles';
import inputStyles from '../styles/inputStyles';

/**
 *
 * lit-element version made by lev.becker.95@gmail.com, based on:
 * @preserve
 * html-duration-picker.js
 *
 * @description Turn an html input box to a duration picker, without jQuery
 * @version [AIV]{version}[/AIV]
 * @author Chif <nadchif@gmail.com>
 * @license Apache-2.0
 *
 */

@customElement('duration-picker')
// eslint-disable-next-line @typescript-eslint/no-unused-vars
class DurationPicker extends LitElement {
    @property({ type: String }) value: string;
    @property({ type: String }) label: string;
    @property({ type: Boolean }) required: boolean;
    @property({ type: Boolean }) hideButtons: boolean;
    @property({ type: Boolean }) hideSeconds = false;
    @property({ type: String }) private adjustmentMode: string;
    @property({ attribute: false }) private intervalId: ReturnType<typeof setTimeout>;
    @property({ attribute: false }) private picker: HTMLInputElement;
    @property({ type: String }) durationMin: string;
    @property({ type: String }) durationMax: string;
    @property({ type: String }) durationDefault: string;

    constructor() {
        super();
    }

    connectedCallback(): void {
        super.connectedCallback();
    }

    firstUpdated() {
        this.picker = this.shadowRoot.querySelector('#duration-picker');
        const currentPickerStyle = window.getComputedStyle(this.picker);
        const pickerRightMargin = currentPickerStyle.marginRight;
        const pickerLeftMargin = currentPickerStyle.marginLeft;
        const totalPickerWidth = currentPickerStyle.width;
        if (!this.value || !this.checkDuration(this.value, this.shouldHideSeconds())) {
            this.insertFormatted(this.picker, this.getInitialDuration());
        }

        const scrollUpBtn = this.shadowRoot.querySelector('#scrollUpBtn');
        const scrollDownBtn = this.shadowRoot.querySelector('#scrollDownBtn');
        scrollUpBtn.setAttribute('style', `height:${this.picker.offsetHeight / 2 - 1}px !important; top: 1px;`);
        scrollDownBtn.setAttribute(
            'style',
            `height:${this.picker.offsetHeight / 2 - 1}px !important; top: ${this.picker.offsetHeight / 2 - 1}px;`,
        );
        const controlsDiv = this.shadowRoot.querySelector('#controlsDiv');
        controlsDiv.setAttribute(
            'style',
            `left: ${parseFloat(totalPickerWidth) - 20}px;
            height:${this.picker.offsetHeight}px;`,
        );
        const controlWrapper = this.shadowRoot.querySelector('#controlWrapper');
        controlWrapper.setAttribute(
            'style',
            `width: ${totalPickerWidth}; margin-left: ${pickerLeftMargin}; margin-right: ${pickerRightMargin};`,
        );
    }

    static get styles(): CSSResult {
        return css`
            ${pickerStyles}
            ${inputStyles}
        `;
    }

    render(): TemplateResult {
        return html`
        <label for="duration-picker">${this.label}</label>
        <div id="controlWrapper" class="controlWrapperStyle html-duration-picker-wrapper">
            <input 
                id="duration-picker" 
                class="html-duration-picker pickerStyles"
                value=${this.value}
                pattern="[0-9]*" 
                ?required=${this.required}
                inputmode="numeric" 
                type="text" 
                aria-label="Duration Picker"
                @drop=${(e: Event) => e.preventDefault()} 
                @keyup=${(e: KeyboardEvent) => this.validateInput(e)} 
                @blur=${(e: Event) => this.insertWithConstraints(e)} 
                @change=${(e: Event) => this.validateInput(e)} 
                @mouseup=${(e: Event) => this.selectFocus(e)} 
                @focus=${(e: Event) => this.selectFocus(e)} 
                @keydown=${(e: KeyboardEvent) => this.handleKeydown(e)} 
            ></input>
        ${
            this.hideButtons
                ? nothing
                : html`
                      <div id="controlsDiv" class="controlsDivStyle controls">
                          <button
                              @mouseleave=${(e: Event) => this.buttonMouseLeave(e)}
                              @mouseup=${(e: Event) => this.buttonMouseUp(e)}
                              @keyup=${(e: KeyboardEvent) => this.buttonKeyUp(e)}
                              @keydown=${(e: KeyboardEvent) => this.buttonKeyDown(e, 'up')}
                              @keypress=${(e: KeyboardEvent) => this.buttonKeyPress(e, 'up')}
                              @mousedown=${(e: Event) => this.buttonMouseDown(e, 'up')}
                              id="scrollUpBtn"
                              type="button"
                              class="scrollStyle scroll-up"
                              aria-label="Increase duration"
                          >
                              <div class="caretStyle caretUpStyle"></div>
                          </button>
                          <button
                              @mouseleave=${(e: Event) => this.buttonMouseLeave(e)}
                              @mouseup=${(e: Event) => this.buttonMouseUp(e)}
                              @keyup=${(e: KeyboardEvent) => this.buttonKeyUp(e)}
                              @keypress=${(e: KeyboardEvent) => this.buttonKeyPress(e, 'down')}
                              @mousedown=${(e: Event) => this.buttonMouseDown(e, 'down')}
                              id="scrollDownBtn"
                              type="button"
                              class="scrollStyle scroll-down"
                              aria-label="Decrease duration"
                          >
                              <div class="caretStyle caretDownStyle"></div>
                          </button>
                      </div>
                  `
        }
            
        </div>
        `;
    }

    buttonMouseDown = (event: Event, direction: 'up' | 'down') => {
        const picker = this.picker;
        const button = event.target as HTMLButtonElement;
        button.style.transform = 'translateY(1px)';
        event.preventDefault();
        this.changeValue(picker, direction);
        this.intervalId = setInterval(this.changeValue, 200, picker, direction);
    };

    buttonMouseUp = (event: Event) => {
        const picker = this.picker;
        const button = event.target as HTMLButtonElement;
        button.style.transform = 'translateY(0)';
        const adjustmentFactor = this.getAdjustmentFactor();
        this.highlightIncrementArea(picker, adjustmentFactor);
        if (this.intervalId) {
            clearInterval(this.intervalId);
            this.intervalId = undefined;
        }
    };

    buttonMouseLeave = (event: Event) => {
        const picker = this.picker;
        const button = event.target as HTMLButtonElement;
        button.style.transform = 'translateY(0)';
        if (this.intervalId) {
            clearInterval(this.intervalId);
            this.intervalId = undefined;
            const adjustmentFactor = this.getAdjustmentFactor();
            this.highlightIncrementArea(picker, adjustmentFactor);
        }
    };

    // handle enter key to increase value, for better accessibility ux
    buttonKeyPress = (event: KeyboardEvent, direction: 'up' | 'down') => {
        const picker = this.picker;
        const button = event.target as HTMLButtonElement;
        button.style.transform = 'translateY(1px)';
        if (event.key == 'Enter') {
            event.preventDefault();
            this.changeValue(picker, direction);
        }
    };

    buttonKeyDown = (event: KeyboardEvent, direction: 'up' | 'down') => {
        const picker = this.picker;
        if (direction === 'up') {
            if (event.key === 'Tab' && event.shiftKey) {
                this.highlightIncrementArea(picker, 1);
                event.preventDefault();
            }
        }
    };

    buttonKeyUp = (event: KeyboardEvent) => {
        const picker = this.picker;
        if (event.key == 'Enter') {
            const adjustmentFactor = this.getAdjustmentFactor();
            this.highlightIncrementArea(picker, adjustmentFactor);
        }
    };

    // Gets the cursor selection
    getCursorSelection = (event: Event, hideSeconds: boolean) => {
        const target = event.target as HTMLInputElement;
        const selectionStart = target.selectionStart;
        const value = target.value;
        const hourMarker = value.indexOf(':');
        const minuteMarker = value.lastIndexOf(':');
        let cursorSelection;

        // The cursor selection is: hours
        if (selectionStart <= hourMarker) {
            cursorSelection = 'hours';
        } else if (hideSeconds || selectionStart <= minuteMarker) {
            // The cursor selection is: minutes
            cursorSelection = 'minutes';
        } else if (!hideSeconds && selectionStart > minuteMarker) {
            // The cursor selection is: seconds
            cursorSelection = 'seconds';
        }

        return { cursorSelection, hideSeconds, hourMarker, minuteMarker };
    };
    // Gets the time interval (hh or mm or ss) and selects the entire block
    selectFocus = (event: Event) => {
        const picker = event.target as HTMLInputElement;
        const hideSeconds = this.shouldHideSeconds();
        // Gets the cursor position and select the nearest time interval
        const { cursorSelection, hourMarker, minuteMarker } = this.getCursorSelection(event, hideSeconds);

        // Something is wrong with the duration format.
        if (!cursorSelection) {
            return;
        }
        // The cursor selection is: hours
        if (cursorSelection === 'hours') {
            this.adjustmentMode = String(60 * 60);
            picker.setSelectionRange(0, hourMarker);
            return;
        }
        // The cursor selection is: minutes
        if (cursorSelection === 'minutes') {
            const increment = hideSeconds ? 3 : 0;

            this.adjustmentMode = String(60);
            picker.setSelectionRange(hourMarker + 1, minuteMarker + increment);
            return;
        }
        // The cursor selection is: seconds
        if (cursorSelection === 'seconds') {
            this.adjustmentMode = String(1);
            picker.setSelectionRange(minuteMarker + 1, minuteMarker + 3);
            return;
        }
        this.adjustmentMode = 'ss';
        picker.setSelectionRange(minuteMarker + 1, minuteMarker + 3);
        return;
    };

    shouldHideSeconds = () => {
        return this.hideSeconds;
    };

    createEvent = (type: string, option = { bubbles: false, cancelable: false }) => {
        if (typeof Event === 'function') {
            return new Event(type);
        } else {
            const event = document.createEvent('Event');
            event.initEvent(type, option.bubbles, option.cancelable);

            return event;
        }
    };

    // Inserts a formatted value into the input box
    insertFormatted = (inputBox: HTMLInputElement, secondsValue: number) => {
        const hours = Math.floor(secondsValue / 3600);
        secondsValue %= 3600;
        const minutes = Math.floor(secondsValue / 60);
        const seconds = secondsValue % 60;
        const formattedHours = String(hours).padStart(2, '0');
        const formattedMinutes = String(minutes).padStart(2, '0');
        const formattedSeconds = String(seconds).padStart(2, '0');

        const value = `${formattedHours}:${formattedMinutes}`;

        inputBox.value = !this.shouldHideSeconds() ? `${value}:${formattedSeconds}` : value;
        this.value = inputBox.value;
        inputBox.dispatchEvent(this.createEvent('input'));
    };
    highlightIncrementArea = (inputBox: HTMLInputElement, adjustmentFactor: number) => {
        const hourMarker = inputBox.value.indexOf(':');
        const minuteMarker = inputBox.value.lastIndexOf(':');
        const hideSeconds = this.shouldHideSeconds();

        inputBox.focus();
        inputBox.select();

        if (adjustmentFactor >= 60 * 60) {
            inputBox.selectionStart = 0; // hours mode
            inputBox.selectionEnd = hourMarker;
        } else if (!hideSeconds && adjustmentFactor < 60) {
            inputBox.selectionStart = minuteMarker + 1; // seconds mode
            inputBox.selectionEnd = minuteMarker + 3;
        } else {
            inputBox.selectionStart = hourMarker + 1; // minutes mode
            inputBox.selectionEnd = hourMarker + 3;
            adjustmentFactor = 60;
        }

        if (adjustmentFactor >= 1 && adjustmentFactor <= 3600) {
            this.adjustmentMode = String(adjustmentFactor);
        }
    };
    // gets the adjustment factor for a picker
    getAdjustmentFactor = () => {
        let adjustmentFactor = 1;
        if (Number(this.adjustmentMode) > 0) {
            adjustmentFactor = Number(this.adjustmentMode);
        }
        return adjustmentFactor;
    };

    // Change the time value;
    changeValue = (inputBox: HTMLInputElement, direction: 'up' | 'down') => {
        const adjustmentFactor = this.getAdjustmentFactor();
        let secondsValue = this.durationToSeconds(inputBox.value);
        switch (direction) {
            case 'up':
                secondsValue += adjustmentFactor;
                break;
            case 'down':
                secondsValue -= adjustmentFactor;
                if (secondsValue < 0) {
                    secondsValue = 0;
                }
                break;
        }
        const fixedValue = this.matchConstraints(secondsValue);
        this.insertFormatted(inputBox, fixedValue);
        // highlightIncrementArea(inputBox, adjustmentFactor);
    };

    // shift focus from one unit to another;
    shiftFocus = (inputBox: HTMLInputElement, toSide: 'left' | 'right') => {
        const adjustmentFactor = this.getAdjustmentFactor();

        switch (toSide) {
            case 'left':
                this.highlightIncrementArea(inputBox, adjustmentFactor * 60);
                break;
            case 'right':
                this.highlightIncrementArea(inputBox, adjustmentFactor / 60);
                break;
        }
    };

    // Check data-duration for proper format
    checkDuration = (duration: string, hideSeconds: boolean) => {
        const pattern = hideSeconds ? '^[0-9]{2,3}:[0-5][0-9]$' : '^[0-9]{2,3}:[0-5][0-9]:[0-5][0-9]$';
        const regex = RegExp(pattern);
        return regex.test(duration);
    };

    matchConstraints = (duration: number) => {
        const { maxDuration, minDuration } = this.getConstraints();
        return Math.min(Math.max(duration, minDuration), maxDuration);
    };
    durationToSeconds = (value: string): number => {
        const sectioned = value.split(':');
        if (sectioned.length < 2) {
            return 0;
        } else {
            return Number(sectioned[2] || 0) + Number(sectioned[1]) * 60 + Number(sectioned[0]) * 60 * 60;
        }
    };

    getEventKey = (event: Event | KeyboardEvent): string => {
        console.log(event.type);
        if (event.type === 'keyup' || event.type === 'keydown') {
            const keyboardEvent = event as KeyboardEvent;
            return keyboardEvent.key;
        } else {
            return undefined;
        }
    };

    // validate any input in the box;
    validateInput = (event: Event | KeyboardEvent) => {
        const picker = event.target as HTMLInputElement;
        const hideSeconds = this.shouldHideSeconds();
        const { cursorSelection } = this.getCursorSelection(event, hideSeconds);
        const sectioned = picker.value.split(':');

        if (
            this.durationDefault &&
            this.checkDuration(this.durationDefault, hideSeconds) &&
            ((hideSeconds && sectioned.length !== 2) || (!hideSeconds && sectioned.length !== 3))
        ) {
            this.value = this.durationDefault; // fallback to data-duration value
            return;
        }
        if (!hideSeconds && sectioned.length !== 3) {
            this.value = '00:00:00'; // fallback to default
            return;
        } else if (hideSeconds && sectioned.length !== 2) {
            this.value = '00:00'; // fallback to default
            return;
        }
        if (isNaN(Number(sectioned[0]))) {
            sectioned[0] = '00';
        }
        if (isNaN(Number(sectioned[1])) || Number(sectioned[1]) < 0) {
            sectioned[1] = '00';
        }
        if (Number(sectioned[1]) > 59 || sectioned[1].length > 2) {
            sectioned[1] = '59';
        }
        if (
            !hideSeconds &&
            sectioned[1].length === 2 &&
            sectioned[1].slice(-1) === this.getEventKey(event) &&
            cursorSelection === 'minutes'
        ) {
            this.shiftFocus(picker, 'right');
        }
        if (!hideSeconds) {
            if (isNaN(Number(sectioned[2])) || Number(sectioned[2]) < 0) {
                sectioned[2] = '00';
            }
            if (Number(sectioned[2]) > 59 || sectioned[2].length > 2) {
                sectioned[2] = '59';
            }
        }

        this.value = sectioned.join(':');
    };

    insertWithConstraints = (event: Event) => {
        const picker = event.target as HTMLInputElement;
        const duration = picker.value || this.durationDefault;
        const secondsValue = this.durationToSeconds(duration);
        this.insertFormatted(picker, this.matchConstraints(secondsValue));
    };

    handleKeydown = (event: KeyboardEvent) => {
        const picker = event.target as HTMLInputElement;
        const changeValueKeys = ['ArrowDown', 'ArrowUp', 'ArrowLeft', 'ArrowRight', 'Enter'];
        if (changeValueKeys.includes(event.key)) {
            switch (event.key) {
                // use up and down arrow keys to increase value;
                case 'ArrowDown':
                    this.changeValue(picker, 'down');
                    break;
                case 'ArrowUp':
                    this.changeValue(picker, 'up');
                    break;
                // use left and right arrow keys to shift focus;
                case 'ArrowLeft':
                    this.shiftFocus(picker, 'left');
                    break;
                case 'ArrowRight':
                    this.shiftFocus(picker, 'right');
                    break;
                case 'Enter':
                    this.insertWithConstraints(event);
                    picker.blur();
                    break;
            }
            event.preventDefault();
        }

        // Allow tab to change selection and escape the input
        if (event.key === 'Tab') {
            const preAdjustmentFactor = this.getAdjustmentFactor();
            const rightAdjustValue = this.shouldHideSeconds() ? 3600 : 60;
            const direction = event.shiftKey ? 'left' : 'right';

            this.shiftFocus(picker, direction);

            if (
                (direction === 'left' && preAdjustmentFactor < 3600) ||
                (direction === 'right' && preAdjustmentFactor >= rightAdjustValue)
            ) {
                event.preventDefault();
            }
        }

        // The following keys will be accepted when the input field is selected
        const acceptedKeys = ['Backspace', 'ArrowDown', 'ArrowUp', 'Tab'];
        if (isNaN(Number(event.key)) && !acceptedKeys.includes(event.key)) {
            event.preventDefault();
            return false;
        }
    };

    getDurationValue = (name: 'durationMin' | 'durationMax' | 'duration', defaultValue: number) => {
        let value: string;
        switch (name) {
            case 'durationMin':
                value = this.durationMin;
                break;
            case 'durationMax':
                value = this.durationMax;
                break;
            case 'duration':
                value = this.durationDefault;
                break;
            default:
                break;
        }
        if (this.checkDuration(value, this.shouldHideSeconds())) {
            return this.durationToSeconds(value);
        } else {
            return defaultValue;
        }
    };

    getConstraints = () => {
        const minDuration = this.getDurationValue('durationMin', 0);
        const maxDuration = this.getDurationValue('durationMax', Infinity);
        return {
            minDuration,
            maxDuration,
        };
    };

    getInitialDuration = () => {
        const duration = this.getDurationValue('duration', 0);
        return this.matchConstraints(duration);
    };
}
