import { LitElement } from 'lit-element';

export interface RequesterEvent extends CustomEvent {
    detail: {
        key: string;
        instance?: unknown;
    };
}

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
export const Requester = (Base: typeof LitElement) =>
    class Requester extends Base {
        public requestInstance(key: string) {
            const event: RequesterEvent = new CustomEvent('request-instance', {
                detail: { key },
                bubbles: true,
                composed: true,
                cancelable: true,
            });

            this.dispatchEvent(event);

            return event.detail.instance;
        }

        public displaySnackbar(message: string): void {
            const event = new CustomEvent('display-snackbar', {
                detail: {
                    message,
                },
                bubbles: true,
                composed: true,
            });
            this.dispatchEvent(event);
        }

        public setTime(startTime: Date, endTime: Date) {
            const event = new CustomEvent('time-change', {
                detail: {
                    startTime,
                    endTime,
                },
                bubbles: true,
                composed: true,
            });
            this.dispatchEvent(event);
        }

        public addBreak(breakString: string) {
            const event = new CustomEvent('add-break', {
                detail: {
                    breakString,
                },
                bubbles: true,
                composed: true,
            });
            this.dispatchEvent(event);
        }
        public deleteBreak(index: number) {
            const event = new CustomEvent('delete-break', {
                detail: {
                    index,
                },
                bubbles: true,
                composed: true,
            });
            this.dispatchEvent(event);
        }

        public saveDay() {
            const event = new CustomEvent('save-day', {
                bubbles: true,
                composed: true,
            });
            this.dispatchEvent(event);
        }

        public loadDay(dateString: string) {
            const event = new CustomEvent('load-day', {
                detail: {
                    dateString,
                },
                bubbles: true,
                composed: true,
            });
            this.dispatchEvent(event);
        }

        public loadDays(year: number) {
            const event = new CustomEvent('load-days', {
                detail: {
                    year,
                },
                bubbles: true,
                composed: true,
            });
            this.dispatchEvent(event);
        }

        public deleteDay() {
            const event = new CustomEvent('delete-day', {
                bubbles: true,
                composed: true,
            });
            this.dispatchEvent(event);
        }
    };
